'use client';

import React, { createContext, useContext, useEffect, useState, ReactNode } from 'react';
import io from 'socket.io-client';
type ClientSocket = ReturnType<typeof io>;
import { useAuth } from './AuthContext';
import toast from 'react-hot-toast';

interface Notification {
  id: string;
  type: 'hired' | 'rejected' | 'new_bid' | 'message';
  message: string;
  gigId?: string;
  bidId?: string;
  timestamp: Date;
  read: boolean;
}

interface SocketContextType {
  socket: ClientSocket | null;
  notifications: Notification[];
  unreadCount: number;
  addNotification: (notification: Notification) => void;
  markAsRead: (notificationId: string) => void;
  clearAllNotifications: () => void;
  connectToUserRoom: () => void;
}

const SocketContext = createContext<SocketContextType | undefined>(undefined);

export const useSocket = () => {
  const context = useContext(SocketContext);
  if (!context) {
    throw new Error('useSocket must be used within a SocketProvider');
  }
  return context;
};

export const SocketProvider = ({ children }: { children: ReactNode }) => {
  const { user, isAuthenticated } = useAuth();
  const [socket, setSocket] = useState<ClientSocket | null>(null);
  const [notifications, setNotifications] = useState<Notification[]>([]);

  useEffect(() => {
    if (!socket) {
      const SOCKET_URL = process.env.NEXT_PUBLIC_SOCKET_URL || 'http://localhost:5001';

      const newSocket = io(SOCKET_URL, {
        withCredentials: true,
        transports: ['websocket', 'polling'],
      } as any);
      
      setSocket(newSocket);

      // Handle connection errors
      newSocket.on('connect_error', (error: unknown) => {
        console.error('Socket connection error:', error);
      });

      newSocket.on('connect', () => {
        console.log('Socket connected:', newSocket.id);
      });

      return () => {
        newSocket.disconnect();
      };
    }
  }, []);

  // Join user room when authenticated
  useEffect(() => {
    if (socket && user && isAuthenticated) {
      socket.emit('join-user-room', user._id);
      console.log('Joined user room:', user._id);
    }
  }, [socket, user, isAuthenticated]);

  // Listen for notifications
  useEffect(() => {
    if (socket) {
      socket.on('notification', (data: any) => {
        const newNotification: Notification = {
          id: Date.now().toString(),
          type: data.type || 'info',
          message: data.message,
          gigId: data.gigId,
          bidId: data.bidId,
          timestamp: new Date(data.timestamp || Date.now()),
          read: false,
        };

        // Add notification to state
        setNotifications(prev => [newNotification, ...prev]);

        // Show toast based on notification type
        switch (data.type) {
          case 'hired':
            toast.success(data.message, {
              duration: 5001,
              icon: 'üéâ',
            });
            break;
          case 'rejected':
            toast.error(data.message, {
              duration: 4000,
            });
            break;
          default:
            toast(data.message, {
              icon: 'üîî',
            });
        }
      });

      return () => {
        socket.off('notification');
      };
    }
  }, [socket]);

  const addNotification = (notification: Notification) => {
    setNotifications(prev => [notification, ...prev]);
  };

  const markAsRead = (notificationId: string) => {
    setNotifications(prev =>
      prev.map(notification =>
        notification.id === notificationId
          ? { ...notification, read: true }
          : notification
      )
    );
  };

  const clearAllNotifications = () => {
    setNotifications([]);
  };

  const connectToUserRoom = () => {
    if (socket && user) {
      socket.emit('join-user-room', user._id);
    }
  };

  const unreadCount = notifications.filter(n => !n.read).length;

  const value = {
    socket,
    notifications,
    unreadCount,
    addNotification,
    markAsRead,
    clearAllNotifications,
    connectToUserRoom,
  };

  return (
    <SocketContext.Provider value={value}>
      {children}
    </SocketContext.Provider>
  );
};